import { AppBar, Box, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, FormControl, Grid, IconButton, InputLabel, MenuItem, Select, Toolbar, Typography } from "@mui/material";
import PrintIcon from '@mui/icons-material/Print';
import { useContext } from "react";
import { MainContext } from "../Contexts/MainContext";

export default function Header() {
    const { handlePrint,
        selectedCodexColor,
        selectedCodex,
        cartes,
        calculateTotalValue,
        maxCarte,
        calculateCarteValue,
        handleOpenCodexSelection,
        handleCloseChange,
        handleClickOpenChange,
        openChange,

    } = useContext(MainContext)
    return (

        <AppBar position='sticky' style={{ backgroundColor: selectedCodexColor, transition: 'background-color 1s ease' }} color='info' >
            <Toolbar sx={{
                justifyContent: "space-between",
                transition: "backgroundColor 1s ease",
            }}>
                <Grid container direction="row"
                    alignItems="center">
                    <Grid item xs={12} md={2}>
                        <img src={process.env.PUBLIC_URL + '/logo10_weathering.png'} alt="Logo" sx={{ displayPrint: 'none' }} />
                    </Grid>
                    <Grid item xs={12} md={2}>
                        {selectedCodex && (
                            <>
                                <Button variant="outlined" color="inherit" onClick={handleClickOpenChange}>Changer de codex</Button>
                                <Dialog open={openChange} onClose={handleCloseChange}>
                                    <DialogTitle >
                                        {"Changer de Codex"}
                                    </DialogTitle>
                                    <DialogContent>
                                        <DialogContentText >
                                            Attention, changer de codex va effacer votre liste actuelle
                                        </DialogContentText>
                                    </DialogContent>
                                    <DialogActions>
                                        <Button onClick={handleCloseChange}>Refuser</Button>
                                        <Button onClick={handleOpenCodexSelection} autoFocus>Accepter</Button>
                                    </DialogActions>
                                </Dialog>
                            </>
                        )}
                    </Grid>
                    <Grid item xs={12} md={3}>
                        <Typography variant='subtitle1'>
                            BLM : {maxCarte.formation} <b>{calculateCarteValue(maxCarte)}</b> pts
                        </Typography>
                    </Grid>
                    <Grid item xs={12} md={2}>
                        <Typography variant='subtitle1'>
                            Nombre de formations : {cartes.length}
                        </Typography>
                    </Grid>
                    <Grid item xs={6} md={2}>
                        <Typography variant='subtitle1'>
                            Points total : <b>{calculateTotalValue()}</b>
                        </Typography>
                    </Grid>
                    <Grid item xs={6} md={1}>
                        <IconButton
                            size="large"
                            edge="start"
                            color="inherit"
                            aria-label="menu"
                            sx={{ mr: 2 }}
                            onClick={handlePrint}
                        >
                            <PrintIcon />
                        </IconButton>
                    </Grid >
                </Grid >
            </Toolbar >
        </AppBar >

    )

}